@font-face {
    font-family: 'iconfont';
    src: url('/src/font/iconfont.ttf?t=1672760840606') format('truetype');
}

.iconfont {
    font-family: "iconfont", serif !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background: azure;
}

li {
    list-style: none;
}

.nav {
    width: 100%;
    height: 40px;
    background-color: #8c6d7d;
}

.box {
    width: 100px;
    height: 100px;
    background: cadetblue;
    text-align: center;
    margin: 25% auto;
    color: beige;
}

.box p {
    padding: calc(50% - 16px) 0;
}

.imgs {
    display: flex;
    width: max-content;
    height: 100px;
    margin: calc(50% - 3 * 100px) auto;
}

.imgs li img {
    width: 100%;
    height: 100px;
}

.imgs li:nth-child(0) {
    background-image: url("/src/images/1.jpg");
}

.imgs li:nth-child(1) {
    background-image: url("/src/images/2.jpg");
}

.imgs li:nth-child(2) {
    background-image: url("/src/images/3.jpg");
}

.imgs li:nth-child(3) {
    background-image: url("/src/images/4.jpg");
}

.icon {
    position: fixed;
    right: 0;
    top: 100px;
}

.icon li span {
    display: flex;
    flex-direction: column;
    color: #8c6d7d;
    font-size: 2rem;
    margin: 25% 0;
}
